.button{
    background-color: blueviolet;
    border-radius: 4px;
    text-align: center;
    margin: 5px;
    width: 120px;
    height: 30px;
    overflow: hidden;
    line-height: 30px;
    box-shadow: 0px 0px 3px rgb(112, 103, 103);
    transition: 0.40s;  
    position: relative;
    cursor: pointer;
}
.button:hover{
    box-shadow: 0px 0px 10px rgb(95, 92, 92);
}
.ripple{
    position: absolute ;
    transform: scale(0);
    width: 110px;
    height: 110px;
    border-radius: 50%;
    animation: ripple 500ms linear;
    background:rgba(255, 255, 255, 0.692);
}
@keyframes ripple {
    to{
        transform: scale(3);
        opacity: 0;
    }
}

