@font-face {
  font-family: intense;
  src: url("res/fonts/Tapestry-Regular.ttf") format("truetype");
}
.intenseFont{
  font-family: intense;
}

body {
  margin: 0;
  padding: 0;
  font-family: intense,-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("res/images/leafs.jpg");
  background-size: cover;
  background-attachment: fixed;
  user-select: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

