
header{
  display: flex;
  width:100%;
  height:45px;
  background-color: rgba(106, 106, 173, 0.76);
  box-shadow: 0px 2px 8px rgb(100, 91, 91);
  font-weight:600;
  font-size: 30px;
  justify-content: space-around;
  color: rgba(43, 226, 58, 0);
  
}
span.headertext{
  background-image: linear-gradient(90deg,rgb(112, 253, 56),rgb(68, 253, 244),rgb(255, 0, 255));
  background-clip: text;
}

/*----------       CSS FOR SEARCH BOX                -----------*/

.searchbox{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80vw;
  height: 150px;
  
  margin: auto auto;
  margin-top: calc(30vh - 45px);
  border-radius: 20px;
  /* box-shadow: 0px 0px 8px white; */
}

.clear{
  background-color: rgb(255, 91, 118);
  border-radius: 4px;
  text-align: center;
}
/*                   CSS FOR SEARCH BOX                */
.inputcont{
  border: 2px solid red;
  display: flex;
  width:90%;
  align-items: center;
  padding: 5px;
  margin: 20px;
  border-radius: 9px;
}
::placeholder{
  color: white;
  opacity: 0.7;
}

.iconanim{
  transform: scale(0.80);
  transition: transform 0.5s;
}
.iconanim:hover{
  transform: scale(1.1);
}

.makeVisible{
  animation: invisible 2s reverse;
  pointer-events:all;
}
.makeInvisible{
  animation: invisible 1s forwards;
  pointer-events: none;
}
.fallAnimation{
  animation: fall 3s forwards;
}

@keyframes invisible {
  from{
    opacity: 1;
  }
  to{
    opacity: 0.1;
  }
}

@keyframes fall {
  0%{
   transform:rotateZ(0 deg);
   background-color:red;
  }
  100%{
    transform:rotateZ(90 deg);
    background-color:blue;
  }
}


@media (min-width:800px) {

  /*    SEARCHBOX  */
    .searchbox{
      width: 640px;
    }
}
