.loadContainer{
    animation: vis 1500ms forwards;
    display: flex;
    justify-content:space-evenly;
    align-items: center;
    position: absolute;
    top:20vh;
    left:10vw;
    width: 80vw;
    height:60vh ;
    background-color: rgba(240, 232, 232, 0.788);
    border-radius: 20px;
    box-shadow: 0px 0px 5px wheat;
    flex-direction:column;
    font-weight: 700;
}
.loading{
    width: 100px;
    height: 100px; 
}
.runner{
    width:30px;
    height: 30px;
    box-shadow: -3px -3px 2px black;
    background-color: red;
    animation: load 2s ease-in-out infinite forwards;
}

.Download{
    width:140px;
    height:40px;
    background-color:red;
    display: flex;
    justify-content:space-evenly;
    align-items: center;
    border-radius: 5px;
}
.cancel{
    position: relative;
    top:-8vh;
    left:33vw;
}



@keyframes load{
    0%{
        transform: translateX(0pz) rotateZ(0deg);
        background-color:rgb(255, 0, 0);
       
    }
    25%{
        transform: translateX(70px) rotateZ(90deg);
        background-color: rgb(0, 255, 0);
       
    }
    50%{
        transform: translateX(70px) translateY(70px) rotateZ(180deg);
        background-color:rgb(29, 70, 255);
    }
    75%{
        transform: translateY(70px) translateX(0%) rotateZ(270deg);
        background-color:orange;
    }
    100%{
        transform:  rotateZ(360deg);
    }
}


@keyframes vis {
    from{
        opacity: 0;
        
    }
    to{
        opacity: 1;
        background:linear-gradient(45deg,rgb(255, 42, 244),rgb(11, 255, 11));
    }
}



@media (min-width:800px){
      .loadContainer{
        width: 640px;
        left:calc(50vw - 320px)
      }
      .cancel{
          left:280px;
      }
}